body {
    margin: 0;
    padding: 0;
    background-color: #161616;
}

section {
    display: grid;
    place-content: center;
    height: 20vh;

    @include tablet {
        display: none;
    }

    @include phone {
        display: none;
    }

    @include small-phone {
        display: none;
    }
}

.img-carousel {
    display: flex;
    flex-direction: row;
    gap: 6rem;
    max-height: 95vh;
    height: auto !important;

    @include tablet {
        flex-direction: column;
        max-height: 100%;
        gap: 0.5rem;
    }

    @include phone {
        flex-direction: column;
        max-height: 100%;
        gap: 0.5rem;
    }

    @include small-phone {
        flex-direction: column;
        max-height: 100%;
        gap: 0.5rem;
    }
}

.spacer {
    width: 70vw;
    flex-shrink: 0;
}

.slide {
    position: relative;
    height: 100%;
}

.background-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
    pointer-events: none;
    border-radius: 10px;
    display: none;

    @include tablet {
        display: block;
    }

    @include phone {
        display: block;
    }

    @include small-phone {
        display: block;
    }
}

/* Style de la carte */
.card {
    border-radius: 10px;
    max-width: 100%;
    margin: 30px auto;
    margin-bottom: 0px;
    text-align: center;

    .content_card {
        position: absolute;
        bottom: 5%;
        left: 5%;
        transform: translate(0, 0);
        font-weight: bold;
        color: rgb(226, 226, 226);
        padding: clamp(1rem, 2vw, 4rem);
        padding-left: 0;
        padding-bottom: 5px;
        border-radius: 5px;
        display: none;
        flex-direction: column;
        align-items: flex-start;
        z-index: 999;
        max-width: 90vw;

        p {
            display: flex;
            flex-wrap: wrap;
            text-align: left; 
            span {
                background: rgb(105, 104, 105);
                padding: 4px 10px;
                border-radius: 50px;
                text-transform: capitalize;
                font-size: 11px;
                margin-right: 6px;
                color: white;
                font-weight: 500;
                white-space: nowrap;
                margin: 2px;
            }
        }


        @include tablet {
            display: flex;
        }

        @include phone {
            display: flex;
        }

        @include small-phone {
            display: flex;
        }
    }
}

.card:hover .content_card {
    display: flex;
}

.card:hover .background-overlay {
    display: block;
}

/* Style du titre */
.title {
    font-weight: bold;
    color: #6E07F3;
    padding-top: 5px;
    /* Couleur sombre pour le texte */
}

/* Style des descriptions */
.description {
    color: rgb(80, 80, 80);
    /* Couleur gris pour le texte */
    margin-top: 10px;
}

.text-overlay {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-weight: bold;
    color: rgb(226, 226, 226);
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
    background-color: rgba(0, 0, 0, 0.5);
    padding: 5px;
    border-radius: 5px;
    display: none;
    /* Caché par défaut */
}

.slide:hover .text-overlay {
    display: block;
    /* Affiché au survol de l'image */
}

.carousel-controls {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0 20px;
}

.carousel-controls button {
    background-color: rgba(255, 255, 255, 0.6);
    border: none;
    padding: 10px 20px;
    color: #161616;
    cursor: pointer;
    border-radius: 50%;
    transition: background-color 0.3s;
}

.carousel-controls button:hover {
    background-color: rgba(255, 255, 255, 0.9);
}

.img-carousel img {
    transition: filter 0.3s ease;
    height: 70vh;
    border-radius: 10px 10px 10px 10px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.4);
    transition: transform 0.3s ease-in-out;
    object-fit: cover;

    @include tablet {
        max-width: 90vw;
        width: 100%;
        height: 35vh;
    }

    @include phone {
        max-width: 90vw;
        width: 100%;
        height: 35vh;
    }

    @include small-phone {
        max-width: 90vw;
        width: 100%;
        height: 35vh;
    }
}